/* eslint-disable */
<template>
  <div class="search-screen">
    <header class="app-header">
      <h1>Search</h1>
    </header>

    <main class="search-content">
      <div class="search-bar-container">
        <i class="ri-search-line search-icon"></i>
        <input
          type="text"
          class="search-input"
          placeholder="Search photos, videos, people, places"
          v-model="searchQuery"
          @keyup.enter="performSearch"
        />
        <button v-if="searchQuery" @click="performSearch" class="search-action-button">Search</button> <!-- UI: SearchScreen.vue -> SearchResults.vue -->
      </div>

      <section class="recent-searches">
        <h2>Recent Searches</h2>
        <div class="tags-container">
          <span v-for="term in recentSearches" :key="term" class="tag" @click="selectRecentSearch(term)">
            {{ term }}
          </span>
        </div>
      </section>

      <section class="categories">
        <h2>Categories</h2>
        <div class="category-grid">
          <div v-for="category in categories" :key="category" class="category-item" @click="selectRecentSearch(category)">
            <i :class="getCategoryIcon(category)"></i>
            <span>{{ category }}</span>
          </div>
        </div>
      </section>
    </main>

    <footer class="bottom-nav">
      <div class="nav-item" @click="showModal">
        <i class="ri-image-line"></i>
        <span>Library</span>
      </div>
      <div class="nav-item" @click="showModal">
        <i class="ri-heart-line"></i>
        <span>For You</span>
      </div>
      <div class="nav-item" @click="showModal">
        <i class="ri-layout-grid-line"></i>
        <span>Albums</span>
      </div>
      <div class="nav-item active">
        <i class="ri-search-fill"></i>
        <span>Search</span>
      </div>
    </footer>
    <modal ref="errorModal"></modal>
  </div>
</template>

<script>
import { AppData } from '@/AppData';
import { navigateTo } from '@/navStackAndLongTap';

export default {
  name: 'SearchScreen',
  data() {
    return {
      searchQuery: '',
      recentSearches: ['July 4, 2023', 'Beach', 'Birthday Party', 'Mountains'],
      categories: ['People', 'Places', 'Selfies', 'Videos', 'Screenshots', 'Favorites'],
    };
  },
  methods: {
    showModal() {
      this.$refs.errorModal.showModal();
    },
    performSearch() {
      if (this.searchQuery.trim() === '') return;
      AppData.applyFilters(this.searchQuery.trim());
      navigateTo(this.$router, 'SearchResults'); // UI: SearchScreen.vue -> SearchResults.vue
    },
    selectRecentSearch(term) {
      this.searchQuery = term;
    },
    getCategoryIcon(category) {
      const icons = {
        'People': 'ri-user-line',
        'Places': 'ri-map-pin-line',
        'Selfies': 'ri-camera-lens-line',
        'Videos': 'ri-video-line',
        'Screenshots': 'ri-crop-line',
        'Favorites': 'ri-star-line',
      };
      return icons[category] || 'ri-folder-line';
    }
  },
};
</script>

<style scoped>
.search-screen {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

.app-header {
  padding: 16px;
  text-align: left;
  border-bottom: 1px solid #e0e0e0;
}

.app-header h1 {
  font-size: 28px;
  font-weight: bold;
  margin: 0;
}

.search-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
}

.search-bar-container {
  display: flex;
  align-items: center;
  background-color: #f0f0f0;
  border-radius: 10px;
  padding: 8px 12px;
  margin-bottom: 24px;
}

.search-icon {
  font-size: 20px;
  color: #8e8e93;
  margin-right: 8px;
}

.search-input {
  flex: 1;
  border: none;
  background: transparent;
  font-size: 16px;
  outline: none;
  padding: 4px 0;
}

.search-input::placeholder {
  color: #8e8e93;
}

.search-action-button {
  background: none;
  border: none;
  color: #007aff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  padding: 4px 8px;
}

h2 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 12px;
}

.recent-searches, .categories {
  margin-bottom: 24px;
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  background-color: #e0e0e0;
  color: #007aff;
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 14px;
  cursor: pointer;
}

.category-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.category-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 12px;
  background-color: #f9f9f9;
  border-radius: 10px;
  border: 1px solid #e8e8e8;
  cursor: pointer;
}

.category-item i {
  font-size: 24px;
  color: #007aff;
  margin-bottom: 8px;
}

.category-item span {
  font-size: 16px;
  font-weight: 500;
}

.bottom-nav {
  display: flex;
  justify-content: space-around;
  border-top: 1px solid #e0e0e0;
  padding: 8px 0;
  background-color: #f8f8f8;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #8e8e93;
  cursor: pointer;
  flex: 1;
}

.nav-item i {
  font-size: 24px;
  margin-bottom: 2px;
}

.nav-item span {
  font-size: 10px;
}

.nav-item.active {
  color: #007aff;
}
</style>